<template>
	<div class="cartNav_box">
		<div class="cart_swiper">
			<van-swipe>
				<van-swipe-item class="van_swipe" v-for="(item,index) in list" :key="index">
					<ul>
						<li v-for="(val,key) in item" :key="key">
							<img :src="`//fuss10.elemecdn.com/`+val.image_url" alt="">
							<p>{{val.title}}</p>
						</li>
					</ul>
				</van-swipe-item>
			</van-swipe>
		</div>
	</div>
</template>

<script>
	// import {Toast} from 'vant'
	export default {
		name: "cartNav",
		data(){
			return{
				list:[]
			}
		},
		//方法
		methods:{
		   async getData(){
			   this.$axios.get('http://elm.cangdu.org/v2/index_entry?geohash=45.748924,126.606249&group_type=1&flags[]=F').then(res =>{
				   console.log(res)
				   if(res.data.length>0){
					   //转换为数组
					   let arrList = res.data.slice(0)
					   let Swipe = []
					   for(let i=0,j=0; i<res.data.length; i+=8,j++){
						   Swipe[j]=arrList.splice(0,8)
					   }
					   this.list=Swipe;
				   }
			   })
		   }

		},
		mounted(){
			this.getData()
		},

	}
</script>

