<template>
	<div class="cartList_box">
		<div class="cartList_shop">
			<p>
				<span class="el-icon-s-shop"></span>
				<span>附近商家</span>
			</p>
		</div>
		<van-list
				v-model="loading"
				:finished="finished"
				finished-text="没有更多了"
				@load="onLoad"
		>
			<ul>
				<router-link :to="{path:'/cart/cartdetails',query:{cartdetails:item.id}}" tag="li" v-for="(item,index) in list" :key="index">
					<img :src="`//elm.cangdu.org/img/`+item.image_path" alt="">
					<div class="cartList_text">
						<div class="cartList_right">
							<p><span>品牌</span><b>{{item.name}}</b></p>
							<p><span>
							<van-rate
									v-model="item.rating"
									allow-half
									void-icon="star"
									void-color="#eee"
									size="10px"
							/>
						</span>
								<span>{{item.rating}}</span>
							</p>
							<p>
								<span>￥{{item.float_minimum_order_amount}}元起送/</span>
								<span>配送费约￥{{item.float_delivery_fee}}元</span>
							</p>
						</div>
						<div class="cartList_left">
							<div>
								<p><span>保</span><span>准</span><span>票</span></p>
								<p><span>蜂鸟专送</span><span>准时达</span></p>
								<p><span>{{item.distance}}/</span><span>{{item.order_lead_time}}</span></p>
							</div>
						</div>
					</div>
				</router-link>
			</ul>
		</van-list>

	</div>
</template>

<script>
	export default {
		name: "cartList",
		data(){
			return{
				list:[],
				loading: false,
				finished: false,
				num:0
			}
		},
		methods:{
			onLoad(){
				this.num+=10
				this.$axios.get("http://elm.cangdu.org/shopping/restaurants?latitude=45.748924&longitude=126.606249&offset="+this.num+"&limit=10&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=").then(res =>{
					this.list.push(...res.data)
					setTimeout(()=>{
						this.loading=false
					},2000)
				})
			}
		},
		//方法
		created(){
			this.$axios.get('http://elm.cangdu.org/shopping/restaurants?latitude=45.748924&longitude=126.606249&offset=0&limit=20&extras[]=activities&keyword=&restaurant_category_id=&restaurant_category_ids[]=&order_by=&delivery_mode[]=').then(res =>{
				this.list=res.data
			})
		}
	}
</script>


