<template>
	<div class="cart_box">
		<header>
			<Haednav head_left="" :head_title='this.name' head_right="">
				<router-link  to="/city" slot="search" tag="span">
					<van-icon name="location" />
				</router-link>
				<router-link to="/user" slot="contact" tag="span">
					<van-icon name="contact" />
				</router-link>
			</Haednav>
		</header>
		<div class="cart_cont">
			<div class="cart_nav">
				<Cartnav></Cartnav>
			</div>
			<div class="cart_list">
				<CartList></CartList>
			</div>
		</div>
		<footer>
			<Foot-Nav></Foot-Nav>
		</footer>
	</div>
</template>

<script>
	import Haednav from '../About/headNav'
	import FootNav from '../About/footNav'
	import Cartnav from './cart/cartNav'
	import CartList from './cart/cartList'
	export default {
		name: "cart",
		data(){
			return{
				start:false,
				arr:[],
				name:''
			}
		},
		components:{
			Haednav,
			FootNav,
			Cartnav,
			CartList
		},
		created(){
			this.arr = JSON.parse(localStorage.getItem('Element'))
			for(let i=0; i<this.arr.length; i++){
				this.name = this.arr[i].name
			}
		}
	}
</script>

